// src/Styles/AddImageModal.scss
.add-image-modal {
    background: white;
    padding: 20px;
    border-radius: 8px;
    max-width: 400px;
    margin: auto;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
  
    .close-modal-btn {
      position: absolute;
      top: 10px;
      right: 10px;
      background: none;
      border: none;
      font-size: 24px;
      cursor: pointer;
      color: #333;
  
      &:hover {
        color: #e74c3c;
      }
    }
  
    h2 {
      text-align: center;
      margin-bottom: 20px;
    }
  
    .image-upload {
      text-align: center;
      margin-bottom: 20px;
  
      .image-upload-icon {
        font-size: 50px;
        color: #3498db;
        cursor: pointer;
  
        &:hover {
          color: #2980b9;
        }
      }
  
      .image-preview {
        display: block;
        margin-top: 10px;
        max-width: 100%;
        height: auto;
        border-radius: 8px;
      }
    }
  
    input[type="text"],
    input[type="number"] {
      width: calc(100% - 20px);
      margin-bottom: 15px;
      padding: 10px;
      border: 1px solid #ccc;
      border-radius: 4px;
      font-size: 16px;
    }
  
    .save-btn {
      width: 100%;
      padding: 10px;
      background-color: #2ecc71;
      color: white;
      border: none;
      border-radius: 4px;
      cursor: pointer;
      font-size: 16px;
  
      &:hover {
        background-color: #27ae60;
      }
    }
  }
  
  .add-image-modal-overlay {
    background-color: rgba(0, 0, 0, 0.75);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
  }
  